.progress_bar_loader {
	position: relative;
	height: 100px;
	width: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: red;
	margin:30px 30px;
	float:left;
}
.progress_bar_loader:before {
	content: "";
	/*background: transparent;*/
	position:absolute;
	z-index:100;
	width:90px;
	height:90px;
	border-radius:50%;
	margin:auto auto;
}
progress::-moz-progress-bar { background: transparent; }
progress::-webkit-progress-bar {background: transparent;}
/*progress::-moz-progress-value { background: red; }*/
progress::-webkit-progress-value { background: red; }
.progress_bar_circle {
	border-radius: 100%;
	overflow: hidden;
	padding:0;
}
.progress_bar_spin {
	animation: progress_bar_spin 2s linear infinite;
}
@keyframes progress_bar_spin {
	to {
		transform: rotate(360deg);
	}
}
